
.addBook{
    background-image: url("15.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 92vh;
    width: 100%;
    color:gold;
    text-align: left;
}
.cover{
    height: 92vh;
    width: 100%;
    background-color:rgba(0, 0, 0, 0.568);
    
}
.cover h1{
    padding-top: 20px;
    text-align: center;
}
.form input{
    margin-top: 15px;
    padding-right:150px ;
    background-color: rgba(255, 251, 0, 0.083);
    padding-top: 10px;
    border: 1px solid gold;
    color: whitesmoke;
    font-size: 18px;
}
.form textarea{
    margin-top: 15px;
    padding-right:200px ;
    background-color: rgba(255, 251, 0, 0.083);
    padding-top: 10px;
    border: 1px solid gold;
    color: whitesmoke;
    font-size: 18px;
}

.form button{
    background-color:goldenrod;
    margin-top: 20px;
    padding: 8px 15px 8px 15px;
    color: black;
    border-radius: 10px;
    font-size: 20px;
    margin-left: 700px;
}
